






















































































import { Component, Vue } from 'vue-property-decorator';
import {HttpService} from "@/services/ApiService";


@Component
export default class Clientes extends Vue {
  public urlImgDboy = 'https://todi-upload.s3.amazonaws.com/upload/avatar/';

  public search = ''
  public users: any[] = []
  public isLoading = true

  setClients(data: any){
    console.log(data)
    this.users = data.data;
    this.isLoading = false;
  }


  addDiscount(id: string, idClient: number){
    let input = document.getElementById(id);

    //@ts-ignore
    if(input?.value >= 0 && input?.value <= 100){
      //@ts-ignore
      console.log(input?.value)
      //@ts-ignore
      this.addDiscountClient(idClient, input?.value)
      //@ts-ignore
      input?.value = "";
    }
  }

  addDiscountClient(id: number, amount: number){
    HttpService.post(`clientes/${id}/add/discount` ,
        {'discount': amount},
        this.setItem)
  }

  setItem(data:any){
    let delivery: any = data.data;
    let index = this.users.findIndex(e => e.id == delivery.id);
    if(index != undefined && index > -1){
      //this.deliveries.splice(index, 1, delivery)
      var us =this.users[index];

      us.discount = delivery.discount;
      console.log(index);
      this.$set(this.users, index, us)
    }
  }


  getClients(){
    HttpService.get(`clientes?search=${this.search}&filter[tody_envios]=1&include=deliveriesCount&append=viajes-completados-count` ,
        this.setClients)
  }

  created(){
    this.getClients()
  }

}
